body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: eff3f7;
  height: 100%;
}

html,
body,
.container {
  height: 100%;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: conter;
  align-items: conter;
}

.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
